/* App.css */
.App {
  text-align: center;
}

header, footer {
  background-color: #222;
  color: white;
  padding: 1em;
}

nav ul {
  list-style: none;
  padding: 0;
}

nav ul li {
  display: inline;
  margin: 0 1em;
}

.hero {
  padding: 2em;
  background-color: #5d4037;
  color: #fff;
}

.about, .products, .contact {
  padding: 2em;
}

.product-list {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.product-item {
  border: 1px solid #ccc;
  padding: 1em;
  margin: 1em;
  width: 30%;
  box-sizing: border-box;
}

.product-item img {
  max-width: 100%;
}

.product-item p {
  font-size: 1em;
}
